.login-page {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #E0F2F1;
}

.login-form {
  background-color: #FFFFFF;
  padding: 2rem;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 300px;
}

.login-form h2 {
  text-align: center;
  margin-bottom: 1.5rem;
}


.Form{
  display: flex;
  flex-direction: column;
}

.login-form input {
  padding: 0.5rem;
  margin-bottom: 1rem;
  border: 1px solid #CCCCCC;
  border-radius: 4px;
}

.login-form button {
  padding: 0.5rem;
  margin-bottom: 1rem;
  border: none;
  border-radius: 4px;
  background-color: #26A69A;
  color: white;
  cursor: pointer;
}

.login-form button:hover {
  background-color: #00796B;
}

.loginStatus{
  margin-top: 0.5em;
  color:red;
}
